import React, { useEffect, useState } from 'react';

import { fetchSupplyLists, deleteSupplyList } from '../../tables/tables';
import { navigate } from 'gatsby';
import Layout from '../../components/Layout';

export default function AllLists() {
  const [lists, setLists] = useState<string[]>([]);
  const [loading, setLoading] = useState(true);
  const [deleting, setDeleting] = useState<string | null>(null); // currently deleting list name


  useEffect(() => {
    fetchSupplyLists().then(data => {
      setLists(data);
      setLoading(false);
    });
  }, []);

  const handleDelete = async (listName: string) => {
    const confirmed = window.confirm(`Are you sure you want to delete the list "${listName}"?`);
    if (!confirmed) return;

    setDeleting(listName);
    const success = await deleteSupplyList(listName);
    setDeleting(null);

    if (success) {
      setLists(prev => prev.filter(name => name !== listName));
      alert(`‚úÖ "${listName}" deleted.`);
    } else {
      alert(`‚ùå Failed to delete "${listName}".`);
    }
  };

  return (

      <div style={{ padding: 20, fontFamily: 'Arial' }}>
        <h2>All Supply Lists</h2>
        {loading ? (
          <p>Loading...</p>
        ) : (
          <table border={1} cellPadding={6} cellSpacing={0}>
            <thead>
              <tr>
                <th>#</th>
                <th>List Name</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {lists.map((name, idx) => (
                <tr key={name}>
                  <td>{idx + 1}</td>
                  <td
                    style={{
                      cursor: deleting ? 'not-allowed' : 'pointer',
                      color: deleting ? '#888' : 'black'
                    }}
                    onClick={() => {
                      if (!deleting) navigate(`/lists/${encodeURIComponent(name)}`);
                    }}
                  >
                    {name}
                  </td>
                  <td>
                    <button
                      onClick={() => handleDelete(name)}
                      disabled={deleting === name}
                      style={{
                        padding: '4px 10px',
                        background: deleting === name ? '#ccc' : 'red',
                        color: 'white',
                        cursor: deleting === name ? 'not-allowed' : 'pointer'
                      }}
                    >
                      {deleting === name ? 'Deleting...' : 'Delete'}
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>

  );
}
